import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardMedia from '@material-ui/core/CardMedia';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import PauseIcon from '@material-ui/icons/Pause';
import MuteIcon from '@material-ui/icons/VolumeMute';
import VolumeUpIcon from '@material-ui/icons/VolumeUp';
import VolumeDownIcon from '@material-ui/icons/VolumeDown';
import red from '@material-ui/core/colors/red';
import SkipNextIcon from '@material-ui/icons/SkipNext';
import Grid from '@material-ui/core/Grid';
import { connect } from "react-redux";
import Player from '../../../components/player';
import Paper from '@material-ui/core/Paper';
import Chip from '@material-ui/core/Chip';
import { fromJS } from 'immutable';
import _ from 'lodash';
import Slider from '@material-ui/lab/Slider';
import { palette, spacing, typography } from '@material-ui/system';
import styled from 'styled-components';

const Box = styled.div`${palette}${spacing}${typography}`;

class StationsPage extends React.Component {    

  constructor(props){
    super(props);
  }

  changeStation(id){    
    if (this.props.id === id){            
      this.props.process({propertyNames:["player", "playing"], payload: !this.props.player.playing});
    }
    else if (id < this.props.stations.length && id >= 0) {
      let elm  = this.props.stations.filter(elm=> elm.id ===id)[0];         
      this.props.process({propertyNames:["player"], payload: fromJS({...elm,  playing:true, volume:this.props.player.volume})});
    }
    else {      
      this.props.process({propertyNames:["player"], payload: fromJS({...this.props.stations[0],  playing:true, volume: this.props.player.volume})});
    }
  }  

  changeTagFilter(tag){
    let tagFound = this.props.selectedTags.filter(tagInside => tagInside === tag)

    if (tagFound.length > 0){
      let newSelectedTags = this.props.selectedTags.filter(tagInside => tagInside !== tag)

      this.props.process({propertyNames:["selectedTags"], payload:fromJS(newSelectedTags)})
    }
    else {
      this.props.selectedTags.push(tag)
      this.props.process({propertyNames:["selectedTags"], payload:fromJS(this.props.selectedTags)})
    }    
  }

  changeVolume(event, value){
    this.props.process({propertyNames:["player", "volume"], payload: (value/100)})    
  }

  render() {

    let classes = {
      card: {
        display: 'flex',
      },
      card2: {
        display: 'flex',
        maxWidth: 150,
      },
      cardActive: {
        display: 'flex',
        maxWidth: 150,
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
      },
      details: {
        display: 'flex',
        flexDirection: 'column',
      },
      content: {
        flex: '1 0 auto',
        padding:10,
        margin:20,        
      },
      cover: {
        width: 50,
        height:50
      },
      controls: {
        display: 'flex',
        alignItems: 'center',
        alignContents: 'center'               
      },
      playIcon: {
        height: 65,
        width: 65,
      },
      muteIcon: {
        height: 65,
        width: 65
        
      },
      iconHover: {
        margin: 10,
        '&:hover': {
          color: red[800],
        },
      },
      bigAvatar: {
        margin: 10,
        width: 80,
        height: 80,
        objectFit: 'cover'
      },
      chipWrapper: {
        padding:5,
        margin:5
      },
      slider: {
        margin: 10,        
        padding:10
      }    
    }
    
    let spacing = 16;

    let theme={
      direction: ""
    }

    let elm;
    if (this.props.id !== -1){
      elm = this.props.stations.filter(elm=> elm.id === this.props.id)[0]      
    }    
    else  {
      elm = this.props.player
    }

    return (
      <Grid container style={classes.root} spacing={16}>
        <Grid item xs={12}>                                     
                <Card style={classes.card}>
                  <div style={classes.details}>
                    <CardContent style={classes.content}>
                      <Typography component="h5" variant="h5">
                        {/* {elm.name} */}
                      </Typography>
                      <Typography variant="subtitle1" color="textSecondary">
                        
                      </Typography>
                      <Slider
                        classes={{ container: classes.slider }}
                        value={this.props.player.volume*100}
                        aria-labelledby="label"
                        onChange={(event, val)=>this.changeVolume(event, val)}
                      />
                    </CardContent>
                    <div style={classes.controls}>
                      <IconButton aria-label="Previous" onClick={()=>this.changeStation(elm.id-1)}>
                        {theme.direction === 'rtl' ? <SkipNextIcon style={classes.playIcon}  color="action"/> : <SkipPreviousIcon style={classes.playIcon}  color="action"/>}
                      </IconButton>
                     {
                        !this.props.player.playing && 
                        (<IconButton aria-label="Play/pause" onClick={()=>this.changeStation(elm.id)}>
                        <PlayArrowIcon style={classes.playIcon} color="primary"/>
                      </IconButton>)
                     }
                     {
                        this.props.player.playing && 
                        (<IconButton aria-label="Play/pause" onClick={()=>this.changeStation(elm.id)}>
                        <PauseIcon style={classes.playIcon} color="primary"/>
                      </IconButton>)
                     }                  
                      <IconButton aria-label="Next" onClick={()=>this.changeStation(elm.id+1)}>
                        {theme.direction === 'rtl' ? <SkipPreviousIcon style={classes.playIcon}  color="action"/> : <SkipNextIcon style={classes.playIcon}  color="action"/>}
                      </IconButton>
                      {
                        !this.props.player.muted && 
                        this.props.player.volume > 0.4 && 
                        (<IconButton aria-label="Mute" onClick={()=>this.props.process({propertyNames:["player", "muted"], payload: !this.props.player.muted})}>
                        <VolumeUpIcon style={[classes.muteIcon]} color="action" style={{ fontSize: 36 }}/>
                      </IconButton>)
                     }
                     {
                        !this.props.player.muted && 
                        this.props.player.volume <= 0.4 && 
                        (<IconButton aria-label="Mute" onClick={()=>this.props.process({propertyNames:["player", "muted"], payload: !this.props.player.muted})}>
                        <VolumeDownIcon style={[classes.muteIcon]} color="action"  style={{ fontSize: 36 }}/>
                      </IconButton>)
                     }
                     {
                        this.props.player.muted && 
                        (<IconButton aria-label="Mute" onClick={()=>this.props.process({propertyNames:["player", "muted"], payload: !this.props.player.muted})}>
                        <MuteIcon style={[classes.muteIcon]} color="error"  style={{ fontSize: 36 }}/>
                      </IconButton>)
                     }
                      <CardMedia
                    style={classes.cover}
                    image={elm.img}
                    title={elm.name}
                  />
                    </div>                    
                   <Player hiden={true} player={this.props.player}/>                                     
                   <Grid item style={classes.chipWrapper}>
                   {this.props.allTags.map(tag =>{
                     let that = this;
                     if (this.props.selectedTags.filter(tagInside => tagInside === tag).length > 0){
                      return (
                        <Chip
                        label={tag}
                        color={"primary"}                                           
                        avatar={"none"}
                        icon={"none"}
                        variant={"default"}                      
                        onClick={()=>this.changeTagFilter(tag)}
                      />
                       )
                     }
                     
                     return (
                      <Chip
                      label={tag}
                      color={"default"}                                           
                      avatar={"none"}
                      icon={"none"}
                      variant={"outlined"}                      
                      onClick={()=>this.changeTagFilter(tag)}
                    />
                     )
                   })   
                  }                
                  </Grid>                   
                  </div>                 
                </Card>                            
        </Grid>        
        <Grid container className={classes.demo} justify="center" spacing={spacing}>
            {this.props.stations.filter(elm => _.intersection(elm.tags, this.props.selectedTags).length == this.props.selectedTags.length).map(elm => (
              <Grid key={elm.id} item>                                                
                <Paper style={this.props.id !==elm.id ? classes.card2 : classes.cardActive} onClick={()=>this.changeStation(elm.id)}>
                    <Avatar alt={elm.name} src={elm.img} style={classes.bigAvatar} classes={{img:"avatarimg"}}/>
                </Paper>                             
              </Grid>
            ))}
          </Grid>
      </Grid>
    )
  }
}

function mapStateToProps(state) {
  return {
    id: state.global.player.toJS().id,
    type: state.global.player.toJS().type,
    src: state.global.player.toJS().src,
    playing: state.global.player.toJS().playing,    
    global: state.global,
    player:state.global.player.toJS(),
    stations:state.global.stations.toJS(),
    selectedTags: state.global.selectedTags.toJS(),
    allTags: state.global.allTags.toJS(),
  };
}

function mapDispatchToProps(dispatch) {
  return {    
    process: ({ propertyNames, payload }) => dispatch({ type: "process", propertyNames, payload }),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(StationsPage);
